.container {
	margin-right: auto;
	margin-left: auto;
	max-width: 980px;
	font-size: 22.5px;

	*zoom: 1;
}
.container:before {
	display: table;
	content: '';
}
.container:after {
	display: table;
	clear: both;
	content: '';
}
.el {
	width: calc(99.99% * 1/2 - 15px);
	height: calc(99.99% * 1/2 - 15px);
	outline: 1px #f00 solid;
}
.el:nth-child(1n) {
	margin-right: 30px;
	margin-bottom: 30px;
	float: left;
	clear: none;
}
.el:last-child {
	margin-right: 0;
	margin-bottom: 0;
}
.el:nth-child(2n) {
	margin-right: 0;
	float: right;
}
.el:nth-child(2n + 1) {
	clear: left;
}
.el:nth-last-child(-n + 2) {
	margin-bottom: 0;
}
.el *:not(input):not(textarea):not(select) {
	background-color: rgba(0, 0, 255, .1);
}
.test {
	overflow: hidden;
	font-size: 480px;
	font-size: 15rem;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.wrap .test {
	background: #fff;

	appearance: none;
}
*,
*:after,
*:before {
	outline: 0;
}
html {
	font-size: 64px;
	font-size: 2rem;
	line-height: .9;
	background-color: #ccc;
}
.bar {
	position: absolute;
	top: 10px;
	right: 20px;
	bottom: 30px;
	left: 40px;
}
.responsive {
	font-size: calc(12px + 9 * ((100vw - 420px) / 860));
}
@media screen and (min-width: 1280px) {
	.responsive {
		font-size: 21px;
	}
}
@media screen and (max-width: 420px) {
	.responsive {
		font-size: 12px;
	}
}
